body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  width: 100vw;
  overflow: hidden auto;
  background-color: rgb(27, 1, 56);
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
a{
  all: unset;
}

/* width */
::-webkit-scrollbar {
    width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: white;
    border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: rgb(0 250 255);
}
:root {
    -webkit-tap-highlight-color: transparent;
}

a:hover {
    all: unset;
}

.navbar {
    display: flex;
    position: fixed;
    padding: 0px;
    z-index: 100;
    top: 0%;
    justify-content: space-between;
    width: 100vw;
    height: 50px;
    background-color: rgb(48 0 92);
    transition: 1000ms;
}

.navbar ul li {
    list-style: none;
    display: inline-block;

}

.navbar ul {
    margin: 0;
    padding: 0;
}

.submenu {
    margin: 0 70px;
}

.list {
    text-align: right;
    display: inline-block;
    cursor: pointer;
}

.navbar ul a:hover {
    /* color: #ffd900; */
    /* color: rgb(0 250 255); */
    border-bottom: 0.2rem solid rgb(0 250 255);

    /* transition: 0.5s; */
    /* scale: 0.95; */
    /* padding: 0.7rem 0; */
}


.active {
    color: rgb(0 250 255);
    border-bottom: 0.2rem solid rgb(0 250 255);
    /* padding: 0.7rem 0; */
}

.list li {
    margin-right: 25px;
}



.top-logo {
    width: 200px;
    line-height: 50px !important;
    margin-left: 100px;
    font-family: 'DynaPuff', cursive;
    font-size: 25px;
}

.mobile {
    display: none;
}

@media all and (max-width: 930px) {

    .navbar {
        display: block;
        overflow: hidden;
    }

    .top-logo {
        margin: 0 30px;
        font-size: 15px;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
    }

    .navbar .list {
        width: 100%;
        text-align: center;

    }

    .navbar .mobile {
        display: inline-block;
        position: absolute;
        top: 14px;
        right: 4%;
    }

    .navbar .list ul {
        margin: 0px;
        padding: 0px;
    }

    .navbar .list ul li {
        display: block;
        margin: 0px 0px;
        margin-bottom: 25px;
    }

    .responsive {
        height: 100%;
        transition: 1000ms;
        /* overflow: hidden; */
    }

    .list {
        /* height: 300px; */
        margin: 30vh 0px;
        /* margin: 50% 0px; */
    }

    .list ul {}

    .list ul li {
        margin-bottom: 30px;
    }



}
.home-page{
