  .scroll{
    position: absolute;
    padding: 0px;
    bottom: 0px; 
    -webkit-animation: mymove 1s; 
            animation: mymove 1s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
  }

  .resume{
    margin-top: 30px;
  }

  .resume .myresume{
    all: unset;
    border: 1px solid white;
    padding: 5px;
  }
  .resume .myresume:hover{
    color: black;
    background-color: white;
    cursor: pointer;
    transition: 0.5s;
  }
  .resume .social-logo{
    margin-bottom: -7px;
  }
  
  @-webkit-keyframes mymove {
    from {bottom: 0px;}
    to {bottom: 8px;}
    from {bottom: 0px;}
  }
  
  @keyframes mymove {
    from {bottom: 0px;}
    to {bottom: 8px;}
    from {bottom: 0px;}
  }

  .scroll ul li{
    list-style: none;
    text-align: center;
  }

  .scroll ul{
    padding: 0px;
  }

